.accordion {
  max-width: 55rem;
  margin: 0 auto;

  .accordion__close-button {
    display: flex;
    justify-content: end;
  }

  .field--name-localgov-paragraphs {
    border-top: 0.1rem solid var(--c-primary);
    margin-top: 1rem;
  }
}

.paragraph--type--localgov-accordion-pane {
  border-bottom: var(--c-primary) 0.2rem solid;
}

.accordion-pane__title {
  display: flex;
  justify-content: space-between;

  h3 {
    margin-bottom: var(--spacing-smaller);
    margin-top: var(--spacing-smaller);
    width: 100%;
  }

  button {
    background-color:var(--color-white);
    width: 100%;
    border: none;
    color: var(--color-grey-dark);
    font-weight: var(--font-semibold);
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:hover,
    &:focus,
    &:active {
      color: var(--c-primary);
      text-decoration: none;

      .accordion-pane__more-icon {
        background-image: url('/themes/custom/brucecastle/images/icons/plus-circle-hover.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 2rem;
        width: 2rem;
        border:none;
      }
    }
  }
}

.accordion-pane__more-icon {
  background-image: url('/themes/custom/brucecastle/images/icons/plus-circle.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 2rem;
  width: 2rem;
  border:none;
}

.accordion-pane__less-icon {
  background-image: url('/themes/custom/brucecastle/images/icons/minus-circle.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 2rem;
  width: 2rem;
  border:none;
}

.accordion-pane__content {
  display: none;
}


.accordion-pane__content--open {
  display: block;
  padding: 0 var(--button-padding-horizontal);
}
